html,
body {
  height: 50px;
  width: 285px;
  margin: 0 auto;
  top: 100px;
  background-color: #282D35;
  background-size: cover;
}

.container {
  align-content: center;
  width: 317px;
  height: 820px;
  left: 116px;
  background: #1A1B21;
  border-radius: 10px 10px 0px 0px;
}

.container>img {
  width: 317px;
  height: 353px;
  left: 116px;
  border-radius: 10px 10px 0px 0px;
}

.author {
  font-family: 'Inter';
  font-style: normal;
  font-weight: 600;
  font-size: 25px;
  text-align: center;
  color: white;
  margin-bottom: -6px;
}

.container>h2 {
  position: relative;
  font-family: 'Inter';
  font-style: normal;
  font-weight: 100;
  font-size: 12.8px;
  text-align: center;
  color: #F3BF99;
}

.container>h3 {
  font-family: 'Inter';
  font-style: normal;
  font-weight: 100;
  font-size: 10.24px;
  line-height: 150%;
  text-align: center;
  color: #F5F5F5;
}

.about {
  position: absolute;
  padding-top: 8px;
  padding-left: 50px;
  font-family: 'Inter';
  font-style: normal;
  font-weight: 600;
  font-size: 16px;
  line-height: 150%;
  color: white;
}

.about__text {
  position: relative;
  width: 242.71px;
  font-family: 'Inter';
  font-style: normal;
  font-weight: 400;
  font-size: 11.2px;
  line-height: 150%;
  color: #DCDCDC;
  padding-top: 40px;
  padding-left: 50px;
}


.interests {
  position: absolute;
  padding-left: 50px;
  font-family: 'Inter';
  font-style: normal;
  font-weight: 600;
  font-size: 16px;
  line-height: 150%;
  color: white;
}

.interests__text {
  position: relative;
  width: 242.71px;
  font-family: 'Inter';
  font-style: normal;
  font-weight: 400;
  font-size: 11.2px;
  line-height: 150%;
  color: #DCDCDC;
  padding-top: 40px;
  padding-left: 50px;
}

.email {
  margin-left: 46px;
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 9px 13px 9px 11px;
  gap: 8px;
  width: 115px;
  height: 34px;
  background: #FFFFFF;
  border: 0px solid;
  border-radius: 6px;
  font-size: 14px;
  box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.05);
  cursor: pointer;
}

.linkedin {
  margin-top: -34px;
  margin-left: 170px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 9px 13px 9px 11px;
  gap: 8px;
  width: 115px;
  height: 34px;
  background: #5093E2;
  border: 0px solid;
  border-color: none;
  border-radius: 6px;
  color: white;
  font-weight: lighter;
  font-family: 'Inter';
  font-size: 14px;
  box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.05);
  cursor: pointer;
}

.email:focus {
  border: 1px solid black;
}

.linkedin:focus {
  border: 1px solid white;
}

.back-to-top {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 10px;
  color: white;
  font-family: 'Inter';
}

.footer {
  position: fixed;
  bottom: 0;
  width: 317px;
  background: #161619;
  border-radius: 0px 0px 10px 10px;
  text-align: center;
  border-radius: 0px 0px 10px 10px;
  background-color: #161619;
}

.fab {
  padding: 4px;
}

.footer__icons {
  display: inline-flex;
  grid-gap: 20px;
  list-style: none;
  color: white;
  margin-right: 40px;
}

.foot__item {
  padding: 3px;
  font-size: 17px;
  background-color: #918E9B;
  border-radius: 5px 5px 5px 5px;
  color: white;
}

.foot__item>a {
  color: #161619;
}
